export default function scroll(upSelector) {
    const upElem = document.querySelector(upSelector);

    window.addEventListener('scroll', () => {
        if (document.documentElement.scrollTop > 1650) {
            upElem.classList.add('animated', 'fadeIn');
            upElem.classList.remove('fadeOut');
        } else {
            upElem.classList.add('fadeOut');
            upElem.classList.remove('fadeIn');
        }
    });

    // Scrolling with raf

    let links = document.querySelectorAll('[href^="#"]');
    
    links.forEach(link => {
        link.addEventListener('click', function(event) {
            event.preventDefault();
    
            let heightTop = document.documentElement.scrollTop, // –ø–æ–ª—É—á–∞–µ–º –∑–Ω–∞—á–µ–Ω–∏–µ —Å–∫–æ–ª—å–∫–æ –≤ –ø–∏–∫—Å–µ–ª—è—Ö –ø—Ä–æ–ª–∏—Å—Ç–∞–ª –ø–æ–ª—å–∑–∏–≤–∞—Ç–µ–ª—å
                hash = this.hash, // –ø–æ–ª—É—á–∞–µ–º —Å—Ç—Ä–æ–∫—É —Å —Å—ã–ª–∫–æ–π
                toBlock = document.querySelector(hash).getBoundingClientRect().top; // –ø–æ–ª—É—á–∞–µ–º –±–ª–æ–∫ –¥–æ –∫–æ—Ç–æ—Ä–æ–≥–æ –º—ã –¥–æ–ª–∂–∏ –ø—Ä–æ–ª–∏—Å—Ç–∞—Ç—å
    
            requestAnimationFrame(step);
            let px = 0;
            function step() {
                px += 60;
                let r = toBlock < 0 ? Math.max(px, toBlock) : Math.min(px, toBlock);
                
                document.documentElement.scrollTo(0, r);
                if (r != heightTop + toBlock) {
                    requestAnimationFrame(step);
                } else {
                    location.hash = hash;
                }
            }
        });
    });
}